import { request } from 'utils';

const prefix = '/api/categories';

export const getBaseNo = ({ query } = {}) =>
  request({
    prefix,
    url: '/QuickLink/GetBaseNo',
    method: 'GET',
    params: { query },
    data: {},
  });

