import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import { InfoCard } from 'components/common';
import {
  getBaseNo,
  getBuildingNo,
  getBuildingRoomNo,
  getRoomTypeNo,
} from 'services/educationQuicklink';
import routeConstant from 'routes/route/routeConstant';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'CS o to',
    service: getBaseNo,
    url: routeConstant.DANH_MUC__CO_SO_DAO_TAO__CO_SO,
  },
  {
    title: 'Ta nh',
    service: getBuildingNo,
    url: routeConstant.DANH_MUC__CO_SO_DAO_TAO__TOA_NHA,
  },
  {
    title: 'Loi phng',
    service: getBuildingRoomNo,
    url: routeConstant.DANH_MUC__CO_SO_DAO_TAO__LOAI_PHONG,
  },
  {
    title: 'Phng hc',
    service: getRoomTypeNo,
    url: routeConstant.DANH_MUC__CO_SO_DAO_TAO__PHONG_HOC,
  },
];

class DashboardCategory extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardCategory {...props} />
  </Page>
);



